.btn {
  font-size: 18px;
  padding: 0.688em 20px;
  border-radius: 5px;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.03);
  -webkit-box-shadow: 3px 4px 5px -3px #000;
  box-shadow: 3px 4px 5px -3px #000;
  min-width: 11.8em;
  &:hover {
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    border-radius: 5px;
    background-color: rgba(42, 94, 145, 0.15);
    -webkit-box-shadow: 5px 6px 15px -6px #000;
    box-shadow: 5px 6px 15px -6px #000;
  }

  a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    display: -webkit-flex;
    -webkit-box-flex: 0;
    -ms-flex: 0 auto;
    flex: 0 auto;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    color: #000;
    font-family: Roboto, sans-serif;
    font-size: 1.063em;
    line-height: 1.25em;
    font-weight: 500;
  }
}

.btn img {
  margin-right: 15px;
}

@media screen and (max-width: 767px) {
  .btn {
    display: none;
  }
}
